import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation"; // Використовуємо API для навігації
import supabase from "../helper/supabaseClient";

function Wrapper({ children }: { children: React.ReactNode }) {
  const [authenticated, setAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);
  const router = useRouter(); // Хук для редиректу

  useEffect(() => {
    const getSession = async () => {
      const supabase 
      try {
        const { data: { session } } = await supabase.auth.getSession();

        if (session) {
          setAuthenticated(true);
        } else {
          setAuthenticated(false);
          router.push("/login"); // Редирект на сторінку логіну
        }
      } catch (error) {
        console.error("Error fetching session:", error);
        setAuthenticated(false);
        router.push("/login");
      } finally {
        setLoading(false);
      }
    };

    getSession();
  }, [router]);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (!authenticated) {
    return null; // Не рендеримо контент до завершення редиректу
  }

  return <>{children}</>;
}

export default Wrapper;

